@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,700&display=swap);
.hero-container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  z-index: -1;
}

.background,
.foreground {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}

.background {
  -webkit-transform: translateZ(-10px) scale(2.1);
          transform: translateZ(-10px) scale(2.1);
  left: -0.5em;
}

.foreground {
  -webkit-transform: translateZ(-5px) scale(1.2);
          transform: translateZ(-5px) scale(1.2);
}

.foreground.left {
  left: -30vw;
  bottom: -15%;
}

.hero-container.left h1,
.hero-container.right h1 {
  color: hsl(0, 0%, 100%);
  font-family: "IBM Plex Sans", sans-serif;
  font-size: clamp(3rem, 10vw, 9rem);
  position: absolute;
  top: 30%;
  right: 0;
  text-align: center;
  max-width: 12ch;
  text-shadow: 0 0 70px hsl(0 0% 100%), 0 0 5px hsl(0 0% 0%);
  -webkit-transform: translateZ(-5px) scale(1.5);
          transform: translateZ(-5px) scale(1.5);
  z-index: -2;
  text-transform: capitalize;
}

.hero-container.right h1 {
  left: -40vw;
  right: auto;
}

.hero-container::before {
  content: "";
  width: 100%;
  position: absolute;
  bottom: 0;
  height: 100px;
  background: linear-gradient(to top, hsl(0 0% 0%), transparent);
}

.foreground.right {
  -webkit-transform: translateZ(-5px) scale(1.2);
          transform: translateZ(-5px) scale(1.2);
  right: -30vw;
}

.hero-container.right {
  z-index: -2;
}

.hero-container.right::after {
  content: "";
  width: 100%;
  position: absolute;
  top: 0;
  height: 100px;
  background: linear-gradient(to bottom, hsl(0 0% 0%), transparent);
}

.hero-container.right .background {
  left: -4em;
}

@media (max-width: 799px) {
  .foreground.right {
    right: -150vw;
    width: 200%;
  }

  .hero-container.right h1 {
    z-index: 1;
    left: -120vw;
  }

  .hero-container.right .background {
    left: -40vw;
  }
}

/* Styling the Paragraph component */
p {
  font-size: 1.2rem;
  padding-block: 1.5rem;
  color: hsl(0 0% 100%);
  font-family: "IBM Plex Sans", sans-serif;
  line-height: 1.7rem;
  letter-spacing: 1px;
  position: relative;
  opacity: 0;
  transition: 0.7s ease-in-out;
}

p:nth-child(even) {
  right: 5vw;
}

p:nth-child(odd) {
  left: 5vw;
}

p.visible {
  opacity: 1;
}

p.visible:nth-child(even) {
  right: 0;
}

p.visible:nth-child(odd) {
  left: 0;
}

/* Styling the */
.segment-container {
  padding-block: 5rem;
  background-color: hsl(0 0% 0%);
  min-height: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

.segment-content {
  width: 90%;
  max-width: 1400px;
}

/* Styling the Footer component */
.footer-container {
  padding-block: 1em 4em;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: hsl(0 0% 0%);
  position: relative;
}

.footer-container span {
  color: hsl(0 0% 100%);
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 1.2rem;
  font-weight: 700;
  position: absolute;
  top: -20%;
  opacity: 0;
  transition: 0.4s ease-in-out;
}

.footer-container span.visible {
  opacity: 1;
  top: 10%;
}

/* Styling the App */

*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  /* outline: 1px solid limegreen; */
}

.app-container {
  width: 100%;
  height: 100vh;
  overflow-y: auto;
  overflow-x: hidden;
  -webkit-perspective: 10px;
          perspective: 10px;
}

